<template>
<div class="row">
<Form ref="form" v-slot="{ errors }" @submit="onSubmit" class="d-flex flex-column col-8 mx-auto">
    <div class="my-3 position-relative">
    <label class="form-label" for="name"></label>
    <Field
        id="name"
        name=""
        type="text"
        class="form-control"
        :class="{ 'is-invalid': errors[''] }"
        placeholder=" " rules="required"
        v-model="user.name"
    >
    </Field>
<error-message name="" class="invalid-feedback" style="position:absolute; left:14px ;bottom:-20px"></error-message>
    </div>

    <div class="my-3 position-relative">
    <label class="form-label" for="tel"></label>
    <Field
        id="tel"
        name=""
        type="text"
        class="form-control"
        :class="{ 'is-invalid': errors[''] }"
        placeholder=""
        :rules="phoneCheck"
        v-model="user.tel"
    >
    </Field>
<error-message name="" class="invalid-feedback" style="position:absolute; left:14px ;bottom:-20px"></error-message>
    </div>

    <div class="my-3 position-relative">
    <label class="form-label" for="email"></label>
    <Field
        id="email"
        name=""
        type="email"
        class="form-control"
        :class="{ 'is-invalid': errors[''] }"
        placeholder="" rules="email|required"
        v-model="user.email"
    >
    </Field>
<error-message name="" class="invalid-feedback" style="position:absolute; left:14px ;bottom:-20px"></error-message>
    </div>

    <div class="my-3 position-relative">
    <label class="form-label" for="address"></label>
    <Field
        id="address"
        name=""
        type="text"
        class="form-control"
        :class="{ 'is-invalid': errors[''] }"
        placeholder=" " rules="required"
        v-model="user.address"
    >
    </Field>
<error-message name="" class="invalid-feedback" style="position:absolute; left:14px ;bottom:-20px"></error-message>
    </div>

    <div class="my-3 position-relative">
    <label class="form-label" for="message"></label>
    <textarea class="form-control" id="message" style="height:100px" v-model="message"></textarea>
    </div>
    <button class="btn btn-primary align-self-end" type="submit"
    :disabled="Object.keys(errors).length > 0 || userDataWatch" :class="{buttonDisabledCursor : Object.keys(errors).length > 0 || userDataWatch}"></button>
</Form>
</div>
</template>
<script>
//  vee-validate 
import { defineRule, Form, Field, ErrorMessage, configure } from 'vee-validate'
//  vee-validate rule
import { required, email } from '@vee-validate/rules'
//  vee-validate 
import { localize, setLocale } from '@vee-validate/i18n'
// 
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json'

defineRule('required', required)
defineRule('email', email)

configure({ // 
  generateMessage: localize({ zh_TW: zhTW }), //  locale
  validateOnInput: true // 
})
// 
setLocale('zh_TW')
export default {
  data () {
    return {
      user: {
        name: '',
        tel: '',
        email: '',
        address: ''
      },
      message: ''
    }
  },
  components: {
    Form,
    Field,
    ErrorMessage
  },
  computed: {
    userDataWatch () {
      // eslint-disable-next-line prefer-const
      let result = false
      Object.keys(this.user).forEach((item) => {
        if (this.user[item].indexOf('') !== -1) {
          result = true
        }
      })
      return result
    }
  },
  methods: {
    phoneCheck (value) {
      const phoneNumber = /^(09)[0-9]{8}$/
      return phoneNumber.test(value) ? true : ''
    },
    onSubmit () {
      const sendData = {
        data: {
          user: {
            name: '',
            tel: '',
            email: '',
            address: ''
          },
          message: '',
          orderid: ''
        }
      }
      sendData.data.user.name = this.user.name
      sendData.data.user.tel = this.user.tel
      sendData.data.user.email = this.user.email
      sendData.data.user.address = this.user.address
      sendData.message = this.message
      this.$http.post(`${process.env.API_API}/api/${process.env.API_PATH}/order`, sendData)
        .then((res) => {
          // 
          this.$http.delete(`${process.env.API_API}/api/${process.env.API_PATH}/carts`).then((res) => {
          }).catch((error) => { console.dir(error) })
          this.orderid = res.data.orderId
          alert(res.data.message)
          console.log(res.data)
        }).catch((error) => { console.dir(error) })
      this.$refs.form.resetForm()
      this.message = ''
    }
  }
}
</script>
